import React, { useCallback, useState, useEffect } from "react";
import axios from "axios";
import { useLocation, useParams, withRouter } from "react-router-dom";
import { toast } from "react-toastify";
import styles from "./MainForm.module.scss";
import "react-toastify/dist/ReactToastify.css";
import FormInput from "./FormInput/FormInput";
import { toastStyles } from "../../../../toastStyle";
import { AiOutlineReload } from "react-icons/ai";

const MainForm = (props) => {
  const location = useLocation();
  const { id } = useParams();
  const action = location.pathname.includes("add")
    ? "add"
    : location.pathname.includes("edit")
    ? "edit"
    : "info";
  const method = action === "add" ? "post" : "put";
  const path = method === "post" ? "/create" : `/update/${id}`;
  const [errorLoadingData, setErrorLoadingData] = useState(null);
  const [isError, setErrorHappened] = useState(true);
  const [nameError, setNameError] = useState("");
  const [salaryError, setSalaryError] = useState("");
  const [ageError, setAgeError] = useState("");
  const [nameState, setName] = useState("");
  const [salaryState, setSalary] = useState("");
  const [ageState, setAge] = useState("");

  useEffect(() => {
    if (id) {
      axios
        .get(`https://dummy.restapiexample.com/api/v1/employee/${id}`)
        .then((response) => {
          let data = response.data.data;
          setName(data.employee_name);
          setSalary(data.employee_salary);
          setAge(data.employee_age);
          setErrorLoadingData(null);
        })
        .catch((err) => {
          toast.error(err.response.data.message, toastStyles);

          setErrorLoadingData(
            <div>
              <AiOutlineReload
                size={"3em"}
                className={styles.reloadButton}
                onClick={() => setErrorHappened((prevState) => !prevState)}
              />
            </div>
          );
        });
    }
  }, [isError, id]);

  const handleSubmit = useCallback(
    (e) => {
      e.preventDefault();
      if (!nameState || !salaryState || !ageState) {
        toast.error("All Fields Should Be Field", toastStyles);
      } else {
        const newEmployee = {
          name: nameState,
          salary: salaryState,
          age: ageState,
        };

        axios({
          method: method,
          url: `https://dummy.restapiexample.com/api/v1${path}`,
          data: JSON.stringify(newEmployee),
        })
          .then((response) => {
            props.history.push("/");
            toast.success(response.data.message, {
              ...toastStyles,
            });
          })
          .catch((error) => {
            if (error.response) {
              toast.error(error.response.data.message + " Please Try Again", {
                ...toastStyles,
              });
            } else if (error.request) {
              toast.error(
                "There Was a Problem with Sending Request, Try Again",
                {
                  ...toastStyles,
                }
              );
            } else {
              console.log(error);
            }
          });
      }
    },
    [ageState, method, nameState, path, salaryState, props.history]
  );

  const handleChange = useCallback(function (e) {
    setNameError("");
    setAgeError("");
    setSalaryError("");
    let val = e.target.value;
    let field = e.target.name;

    if (!val) {
      if (field.includes("name")) setNameError("This Field Should Be Filled");
      if (field.includes("salary"))
        setSalaryError("This Field Should Be Filled");
      if (field.includes("age")) setAgeError("This Field Should Be Filled");
    }

    if (field === "employee_name") {
      if (!/^[a-zA-Z0_ ]*$/.test(val)) {
        setNameError("This Field Should Be A Valid Name");
      } else {
        setName(e.target.value);
      }
    }

    if (field === "employee_salary") {
      if (!/^[0-9]*$/.test(val)) {
        setSalaryError("This Field Should Be A Number");
      } else {
        setSalary(e.target.value);
      }
    }

    if (field === "employee_age") {
      if (!/^[0-9]*$/.test(val)) {
        setAgeError("This Field Should Be A Number");
      } else if (val > 60) {
        setAgeError("Maximum Age is 60 Years Old");
      } else {
        setAge(val);
      }
    }
  }, []);

  return (
    <div className={styles.formContainer}>
      <h2>
        {action === "add" ? "Add New " : action === "edit" ? "Edit " : ""}
        Employee{action === "info" ? " Info" : null}
      </h2>
      <div>{errorLoadingData}</div>
      <form onSubmit={handleSubmit}>
        <FormInput
          onchange={handleChange}
          error={nameError}
          label="Name"
          value={nameState}
          placeHolder={"Please Enter Name"}
          name="employee_name"
        />
        <FormInput
          onchange={handleChange}
          error={salaryError}
          value={salaryState}
          label="Salary"
          placeHolder={"Please Enter Salary"}
          name="employee_salary"
        />
        <FormInput
          onchange={handleChange}
          error={ageError}
          value={ageState}
          label="Age"
          placeHolder={"Please Enter Age"}
          name="employee_age"
        />

        {action === "info" ? null : <input type="submit" value="Submit" />}
      </form>
    </div>
  );
};

export default withRouter(MainForm);
