import { useState,useEffect ,useContext} from "react";

import axios from "axios"
import ReactModal from "react-modal";
import styles from "./InfoModal.module.scss";
import { AiFillCloseCircle } from "react-icons/ai";
import { toastStyles } from "../../../toastStyle"
import { toast } from "react-toastify";
import { AiOutlineReload } from "react-icons/ai";
import {ModalContext} from "../../../contexts";

const InfoModal = (props) => {
   const [errorLoadingData, setErrorLoadingData] = useState(null);
   const [isError, setErrorHappened] = useState(true);

  const {isInfoOpen, setInfoModalOpen,clickedId} = useContext(ModalContext);
  const [{employee_name,employee_age,employee_salary}, setEmployeeInfo] = useState({});


 
  const handleCloseButton = () => {
    setInfoModalOpen(false);
  };
  useEffect(()=>{
  if (isInfoOpen) {
    setEmployeeInfo({})
    axios.get(`https://dummy.restapiexample.com/api/v1/employee/${clickedId}`).then(res=>{setEmployeeInfo(res.data.data)}).catch((err) => {
      toast.error(
        "Could Not Load Employee Data. Please Try Again",
        toastStyles
      );

      setErrorLoadingData(
        <div>
          <AiOutlineReload
            size={"3rem"}
            className={styles.reloadButton}
            onClick={() => setErrorHappened((prevState) => !prevState)}
          />
        </div>
      );
    })
  }
  },[isError,isInfoOpen,clickedId])
useEffect(()=>{
   if (isInfoOpen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = 'unset';
    }
})
  return  (
    <ReactModal  ariaHideApp={false} isOpen={isInfoOpen} className={styles.modalContent} overlayClassName={styles.overlay} >
      <AiFillCloseCircle onClick={handleCloseButton} className={styles.closeButton} size={"3rem"}/>
      
      <h1>Employee Info</h1>
      {errorLoadingData}
      <div>Name: {employee_name}</div>
      <div>Salary: {employee_salary}</div>
      <div>Age: {employee_age}</div>

    </ReactModal>
  );
};

export default InfoModal;
