import React from "react"
import { useLocation } from "react-router-dom";
import styles from "./FormInput.module.scss";
import PropTypes from 'prop-types';

const FormInput =(props) => {
  const location=useLocation()
  const isInfoPage=location.pathname.includes("info")
    return ( <div className={styles.textInputContainer}>
      <label>{isInfoPage&&props.label}</label>
      <input 
      disabled={location.pathname.includes("info")}
      type="text"
      name={props.name}
      placeholder={isInfoPage?"":props.placeHolder}
      onChange={props.onchange}
value={props.value}
    /> {
      !props.error?null:<div>{props.error}</div>
    } </div>);
}
 FormInput.propTypes={
   name:PropTypes.string,
   placeHolder:PropTypes.string,
   onchange:PropTypes.func,
   onblur:PropTypes.func
 }
export default FormInput;