import {useContext} from "react"
import {Link} from "react-router-dom"
import { AiOutlineDelete,AiOutlineEdit ,AiOutlineUser} from "react-icons/ai";
import {ModalContext} from "../../../../contexts"


const TableRow = (props) => {
  const {setInfoModalOpen,setClickedId,setDeleteModalOpen}=useContext(ModalContext)
  const {
    id,
    employee_name,
    employee_salary,
    employee_age,
  } = props;
  const handleClick=(e)=>{

    let tagName=e.target.tagName
    if (tagName!=="svg"&&tagName!=="A"&&tagName!=="path")  {setInfoModalOpen(true);setClickedId(id)}
  }
  return (
    <>
      <tr onClick={handleClick}>
        <td>{id}</td>
        <td>{employee_name}</td>
        <td><AiOutlineUser size="3em" /></td> 
        <td>{employee_salary}</td>
        <td>{employee_age}</td>
        <td>
       <span><Link  to={`/${id}/edit`}><AiOutlineEdit size="2em" /></Link></span>   
         <span> <AiOutlineDelete  size="2em" onClick={()=>setDeleteModalOpen(true)} /></span>
        </td>
      </tr>
    </>
  );
};



export default TableRow;
