import "./scss/mainStyles.scss";
import Table from "./Components/Table/Table";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import AddEditPage from "./Components/Pages/AddEditPage/AddEditPage";
import { ToastContainer } from "react-toastify";
function App() {
  return (
    <Router basename={'/employeeManager/'}>
      <Switch>
        <Route exact path="/">
          <Table />
        </Route>

        <Route path="/add">
          <AddEditPage />
        </Route>
        <Route exact path={`/:id/edit`}>
          <AddEditPage />
        </Route>
      </Switch>
      <ToastContainer />
    </Router>
  );
}

export default App;
