import ReactModal from "react-modal";
import axios from "axios";
import {  toast } from "react-toastify";
import {  useParams } from "react-router-dom";
import { toastStyles } from "../../../toastStyle";
import {  useEffect,useContext} from "react";
import {ModalContext} from "../../../contexts"
import styles from "./DeleteModal.module.scss";

const DeleteModal = () => {
  const {isDeleteModalOpen, setDeleteModalOpen} = useContext(ModalContext);
  const { id } = useParams();
  const handleCancel = () => {
    setDeleteModalOpen(false);
  };
  const handleYes = () => {
    axios
      .delete(`https://dummy.restapiexample.com/api/v1/delete/${id}`)
      .then((response) =>{            setDeleteModalOpen(false);
        toast.info(response.data.message, {
          ...toastStyles
        })
      }
      
      )
      .catch((error) =>
        toast.error(
          "Error: " + error.response.data.message + " Please Try Again.",
          toastStyles
        )
      );
  };
useEffect(()=>{
   if (isDeleteModalOpen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = 'unset';
    }})
  return  (
    <ReactModal
      className={styles.modal}
      overlayClassName={styles.overlay}
      ariaHideApp={false}
      isOpen={isDeleteModalOpen}
    >
      <form>
        <div>Are You Sure You Want to Delete the Employee?</div>
        <input type="button" value="Yes" onClick={handleYes} />
        <input type="button" value="Cancel" onClick={handleCancel} />
      </form>
    </ReactModal>
  );
};

export default DeleteModal;
