import ReactPaginate from "react-paginate";
import styles from "./TableFooter.module.scss";
const TableFooter = ({
  pageNum,
  rowsNumber,
  setRowsNumber,
  setPageNum,
  length,
}) => {
  const handleRowsChange = (e) => {
    setPageNum(0);
    setRowsNumber(+e.target.value);
  };
  return (
    <div className={styles.main}>
      <div className={styles.rowsBar}>
        <span>Rows Per Page:</span>{" "}
        <select onChange={handleRowsChange} defaultValue={6}>
          {" "}
          <option>2</option>
          <option>4</option>
          <option>6</option>
        </select>
      </div>
      <ReactPaginate
        previousLabel={"Previous"}
        nextLabel={"Next"}
        breakLabel={"..."}
        breakClassName={styles.paginationEllipsis}
        pageCount={length / rowsNumber}
        forcePage={pageNum}
        marginPagesDisplayed={2}
        pageRangeDisplayed={1}
        onPageChange={(data) => {
          setPageNum(data.selected);
        }}
        containerClassName={styles.pagination}
        pageClassName={styles.paginationPage}
        previousClassName={styles.paginationPrevAndNext}
        nextClassName={styles.paginationPrevAndNext}
        activeClassName={styles.paginationActive}
      />
    </div>
  );
};

export default TableFooter;
