import TableHeader from "./TableHeader/TableHeader";
import TableRows from "./TableRows/TableRows";
import { useState, useEffect } from "react";
import axios from "axios";
import Header from "../Header/Header";
import { toastStyles } from "../../toastStyle";
import { toast } from "react-toastify";
import TableFooter from "./TableFooter/TableFooter";
import styles from "./Table.module.scss";
import InfoModal from "../Pages/InfoModal/InfoModal";
import {ModalContext} from "../../contexts"
import DeleteModal from "../Pages/DeleteModal/DeleteModal";



const Table = (props) => {
  const [reload, setReload] = useState(true);
  const [fullEmployeesList, setFullEmployeesList] = useState([]);
  const [rowsNumber, setRowsNumber] = useState(6);
  const [pageNum, setPageNum] = useState(0);
  const [clickedId, setClickedId] = useState(0);

  const [isInfoOpen, setInfoModalOpen] = useState(false)
  const [isDeleteModalOpen, setDeleteModalOpen] = useState(false);

  const length = fullEmployeesList.length;
  const states = { pageNum, rowsNumber, setRowsNumber, setPageNum, length };
  useEffect(() => {
    axios
      .get("https://dummy.restapiexample.com/api/v1/employees")
      .then((response) => {
        let data = response.data.data;
        setFullEmployeesList(data);
      })

      .catch((error) => {
        toast.error("Please Try Again", toastStyles);
      });
  }, [reload]);
  const content = !fullEmployeesList.length ? (
    <div className={styles.reloadMessage}>
      <div>Could Not Load Table. Please Try Again</div>
      <button onClick={() => setReload((prevState) => !prevState)}>
        Reload Table
      </button>
    </div>
  ) : (
    <ModalContext.Provider value={{isInfoOpen,setInfoModalOpen,isDeleteModalOpen, setDeleteModalOpen,clickedId,setClickedId}}>  <table>
    <TableHeader />
    <TableRows
      employees={fullEmployeesList.slice(
        pageNum * rowsNumber,
        pageNum * rowsNumber + rowsNumber
      )}
    />
  </table>{" "}
  <TableFooter {...states} />
  <InfoModal/><DeleteModal/>
  </ModalContext.Provider>
    
  );
  return (
    <div className={styles.mainTable}>
      <Header />
      {content}
    </div>
  );
};

export default Table;
